.conta{

    width: 100%;
    height: 100vh;
    background-color: #E3F2FD;
    background-image:url("https://i.postimg.cc/sXmyqkC3/9.jpg");
    background-repeat: no-repeat;
    background-size:cover;
    overflow-y: scroll;
    margin-top: 4rem;
    
    
    
}
.cards{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.contain{
    width: 20rem;
    
    height: max-content;
    border: 3px solid black;
    margin: 2rem;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  
  
}
.image{
    width: 100%;
    height: 15rem;
    object-fit:fill;
}
.price,.number{
    font-weight: 900;
    margin: 1rem;
}
.button{
    margin-left: 7rem;
    color: white;
    background: red;
    border-radius: 0.3rem;
    padding: 0.3rem;
    cursor: pointer;
    margin-bottom: 1rem;
    width: 6rem;

}
.p{
    font-weight: 400;
    overflow: hidden;
    color: rgb(255, 0, 0);
}
.sea{
    margin-left: 1rem;
    width: 50%;
    border-radius: 1rem;
    
    border: none;
   
  
    box-shadow:  3px 3px 13px #000000,
                 -3px -3px 13px #000000;
}
.z{
  margin-top: 12rem;
  text-transform: uppercase;
  font-weight: 900;
}
.loader {
    width: 6em;
    height: 6em;
    font-size: 10px;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  
  .loader .face {
    position: absolute;
    border-radius: 50%;
    border-style: solid;
    -webkit-animation: animate023845 3s linear infinite;
            animation: animate023845 3s linear infinite;
  }
  
  .loader .face:nth-child(1) {
    width: 100%;
    height: 100%;
    color: gold;
    border-color: currentColor transparent transparent currentColor;
    border-width: 0.2em 0.2em 0em 0em;
    --deg: -45deg;
    -webkit-animation-direction: normal;
            animation-direction: normal;
  }
  
  .loader .face:nth-child(2) {
    width: 70%;
    height: 70%;
    color: lime;
    border-color: currentColor currentColor transparent transparent;
    border-width: 0.2em 0em 0em 0.2em;
    --deg: -135deg;
    animation-direction: reverse;
  }
  
  .loader .face .circle {
    position: absolute;
    width: 50%;
    height: 0.1em;
    top: 50%;
    left: 50%;
    background-color: transparent;
    -webkit-transform: rotate(var(--deg));
        -ms-transform: rotate(var(--deg));
            transform: rotate(var(--deg));
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
  }
  
  .loader .face .circle::before {
    position: absolute;
    top: -0.5em;
    right: -0.5em;
    content: '';
    width: 1em;
    height: 1em;
    background-color: currentColor;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 2em,
                  0 0 4em,
                  0 0 6em,
                  0 0 8em,
                  0 0 10em,
                  0 0 0 0.5em rgba(255, 255, 0, 0.1);
            box-shadow: 0 0 2em,
                  0 0 4em,
                  0 0 6em,
                  0 0 8em,
                  0 0 10em,
                  0 0 0 0.5em rgba(255, 255, 0, 0.1);
  }
  
  @-webkit-keyframes animate023845 {
    to {
      -webkit-transform: rotate(1turn);
              transform: rotate(1turn);
    }
  }
  
  @keyframes animate023845 {
    to {
      -webkit-transform: rotate(1turn);
              transform: rotate(1turn);
    }
  }

